.tasks-deshborad button{
  background-color: #f3897b;
}
.tasks-deshborad button:hover{
  color:  #f3897b;
}

.tasks-deshborad {
  height: auto;
  border-radius: 5px;
  direction: rtl;
  text-align: center;
  width: 80%;
  margin: 1rem;
  border: 1px solid black;
  background-color: #e0e0e0;
}

.tasks-deshborad div {
  border-left: 1px solid rgb(34, 33, 33); 
  border-bottom: 1px solid rgb(34, 33, 33); 
  width: 100%;
  height: 5rem; /* גובה קבוע */
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  overflow: hidden; /* מסתיר את התוכן שיוצא מגבולות ה-div */
  text-overflow: ellipsis; /* מציג ... כאשר הטקסט חורג */
  white-space: nowrap; /* מונע שורות חדשות */
}




.title-title-task{
  display: flex;
  flex-direction: row-reverse;
  justify-content:space-around;
  align-items: center;
  text-align: center;
  width: 80%;
  height: 3rem;
  border-radius: 5px;
  border: 1px solid rgb(182, 182, 182);
  background: linear-gradient(to left, #2680eb, #f3897b);
color: white;
font-size: 1rem;
margin-top: 1rem;

}

.title-title-task div{
 border-left: 1px solid white; 
 height: 100%;
 width: 100%;
 text-align: center;
 display: flex;
