.priority{
  color: #fff;
  background: #2680eb;
  border-radius: 100%;
  /* padding: 1rem 0.8rem; */
  width: 1.5rem;
  padding: 0.5rem;
  font-size: 1rem;
  margin-bottom: 1rem;
}


textarea {
  border: 1px solid #ccc;
  text-align: center;
  margin: 0.7rem;
  padding: 0.5rem 0.5rem;
  direction: rtl;
}
.fa-calendar-days{
  color: #bbb;
}

.task-details-form {
  direction: rtl;
  text-align: start;
  width: 90%;
  height: 8rem;
  resize: none;
  line-height: 1.5rem; 
  background-size: 100% 1.55rem;
  padding: 0.5rem;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 0.8rem;
  overflow: hidden;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}



