button {
  background: #2680eb;
  border: none;
  border-radius: 4px;
  box-shadow: 0 3px 1px -2px #0003, 0 2px 2px #00000024, 0 1px 5px #0000001f;
  color: #fff;
  cursor: pointer;
  padding: 0.4rem 1.5rem;
  margin: 0.5rem;
  font-size: 0.875rem;
  transition: background .4s cubic-bezier(.25, .8, .25, 1), box-shadow .28s cubic-bezier(.4, 0, .2, 1);
}

.LogIn {
  /* background-color: #378d58; */
