}

.Registration {
  /* background-color: #378d58; */
  background: linear-gradient(to left, rgb(17, 50, 82), rgb(202, 241, 201));

}



.button-Registration {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.reast-pass {
  cursor: pointer;
  color: #2680eb;
  background-color: #efebeb;
  border-radius: 5px;
